@import url(https://rsms.me/inter/inter.css);
html {
  font-family: 'Inter', sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter var', sans-serif;
  }
}

.page__container {
  width: 882px;
  margin: 0 auto;
}

@media screen and (max-width: 919px) {
  .page__container {
    width: 100%;
  }
}

.page {
  background: #000;
  margin: 0 auto;
  min-height: 100vh;
  font-family: 'Inter', Arial, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.button_type_edit {
  margin-top: 19px;
  margin-left: 24px;
  width: 24px;
  height: 24px;
  background: #000 url(/around-react/static/media/edit.8667ac4a.svg) center/10px 10px
    no-repeat;
  border: 1px solid #fff;
  box-sizing: border-box;
}

.button_type_edit:hover {
  opacity: 0.6;
}

@media screen and (max-width: 919px) {
  .button_type_edit {
    width: 18px;
    height: 18px;
    background-size: 7.5px 7.5px;
    margin-left: 12px;
  }
}

.button_type_edit-avatar {
  background: transparent url(/around-react/static/media/edit.8667ac4a.svg);
  position: relative;
  background-size: 100% 100%;
  border: none;
  height: 26px;
  width: 26px;
  top: -73px;
  right: -47px;
  z-index: -1;
}

@media screen and (max-width: 919px) {
  .button_type_edit-avatar {
    top: -73px;
    right: -128px;
  }
}

.button_type_add {
  width: 150px;
  height: 50px;
  background: #000 url(/around-react/static/media/plus.d86bb2ed.svg) no-repeat center/22px
    22px;
  border: 2px solid #fff;
  border-radius: 2px;
  box-sizing: border-box;
  align-self: center;
}

.button_type_add:hover {
  opacity: 0.6;
}

@media screen and (max-width: 919px) {
  .button_type_add {
    width: 100%;
    background-size: 16px 16px;
    margin-top: 36px;
  }
}

.button_type_like {
  width: 18.13px;
  height: 20.76px;
  margin-top: 21px;
  background: transparent url(/around-react/static/media/like.49570bcb.svg) no-repeat
    center/100%;
  border: none;
}

.button_type_like:hover {
  opacity: 0.6;
}

.button_type_liked {
  background-image: url(/around-react/static/media/liked.75438e94.svg);
}

.button_type_close {
  width: 40.7px;
  height: 40.7px;
  position: relative;
  left: 100%;
  bottom: 40.7px;
  background: transparent url(/around-react/static/media/plus.d86bb2ed.svg) no-repeat
    center/contain;
  border: none;
  transform: rotate(45deg);
}

.button_type_close:hover {
  opacity: 0.6;
}

@media screen and (max-width: 919px) {
  .button_type_close {
    width: 30px;
    height: 30px;
    left: 100%;
    bottom: 30px;
  }
}

.button_type_close_type_image {
  bottom: 0;
}

.button_type_submit {
  width: 358px;
  padding: 0;
  margin-top: 48px;
  height: 50.15px;
  background-color: #000;
  border: none;
  border-radius: 2px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #fff;
}

.button_type_submit:hover {
  opacity: 0.8;
}

@media screen and (max-width: 919px) {
  .button_type_submit {
    font-size: 14px;
    line-height: 17px;
    margin-top: 36px;
    width: 200px;
    height: 40px;
  }
}

.button_type_delete {
  width: 18px;
  height: 19.3px;
  background: transparent url(/around-react/static/media/delete.a274c87c.svg) no-repeat
    center/contain;
  border: none;
  float: right;
  margin-top: 18px;
  margin-right: 15px;
  display: block;
}

.button_type_delete:hover {
  opacity: 0.6;
}

.button {
  transition: opacity 0.2s ease;
  cursor: pointer;
}

.button_type_delete_hidden {
  display: none;
}

.header__logo {
  width: 173px;
  height: 33px;
  background: url(/around-react/static/media/logo.4e8e0a1d.svg) center/173px 33px;
}

@media screen and (max-width: 919px) {
  .header__logo {
    width: 126px;
    height: 24px;
    margin-left: 27px;
    background-size: 126px 24px;
  }
}

.header {
  padding-top: 45px;
  margin: 0 auto;
  height: 74px;
  border-bottom: 1px solid #545454;
}

@media screen and (max-width: 919px) {
  .header {
    padding-top: 28px;
    height: 56px;
  }
}

@media screen and (max-width: 919px) {
  .main {
    width: 282px;
    margin: 0 auto;
  }
}

.profile__data {
  display: flex;
}

@media screen and (max-width: 919px) {
  .profile__data {
    flex-direction: column;
  }
}

.profile__avatar {
  width: 120px;
  height: 120px;
  background: center/cover;
  border-radius: 50%;
  z-index: 2;
  transition: opacity 0.2s ease;
}

.profile__avatar:hover {
  opacity: 0.2;
  z-index: 0;
  cursor: pointer;
}

.profile__avatar:hover + .button_type_edit-avatar {
  z-index: 1;
}

@media screen and (max-width: 919px) {
  .profile__avatar {
    margin: auto;
  }
}

.profile__info {
  margin-left: 30px;
  padding-top: 18px;
  display: flex;
}

@media screen and (max-width: 919px) {
  .profile__info {
    padding-top: 14px;
    margin-left: 32px;
    justify-content: center;
  }
}

.profile__text {
  color: #fff;
  max-width: 460px;
}

@media screen and (max-width: 919px) {
  .profile__text {
    max-width: 250px;
  }
}

.profile__name {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 56px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 919px) {
  .profile__name {
    font-size: 22px;
    line-height: 56px;
    text-align: center;
  }
}

.profile__job {
  margin: 0;
  padding-top: 16px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 919px) {
  .profile__job {
    font-size: 14px;
    line-height: 17px;
    padding-top: 0;
    text-align: center;
  }
}

.profile {
  margin: 0 auto;
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 919px) {
  .profile {
    padding-top: 42px;
    width: 282px;
    flex-direction: column;
  }
}

.places__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.places {
  padding-top: 51px;
}

@media screen and (max-width: 919px) {
  .places {
    padding-top: 36px;
  }
}

.card__image {
  width: 100%;
  height: auto;
  background: no-repeat center/cover;
  padding-top: 100%;
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
}

.card__info {
  display: flex;
  justify-content: space-between;
  margin-left: 21px;
  margin-right: 22.24px;
}

.card__title {
  margin: 0;
  padding-top: 25px;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.card__likes {
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0em;
  text-align: center;
  margin: 0;
  padding-top: 3px;
}

.card {
  margin-bottom: 21px;
  margin-left: 18px;
  width: 282px;
  height: 361px;
  background: #fff;
  border-radius: 10px;
}

.card:nth-of-type(3n + 1) {
  margin-left: 0;
}

@media screen and (max-width: 919px) {
  .card {
    margin-left: 0;
  }
}

.footer__text {
  margin: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
}

@media screen and (max-width: 919px) {
  .footer__text {
    font-size: 14px;
    line-height: 17px;
  }
}

.footer {
  padding-top: 45px;
  padding-bottom: 60px;
}

@media screen and (max-width: 919px) {
  .footer {
    padding-top: 28px;
    padding-bottom: 36px;
    width: 282px;
    margin: 0 auto;
  }
}

.popup__container {
  width: 430px;
  background: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

@media screen and (max-width: 919px) {
  .popup__container {
    width: 240px;
  }
}

.popup__container_type_image {
  background: none;
  box-shadow: none;
  border-radius: 0;
  width: unset;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.popup__title {
  margin: 0;
  margin-left: 36px;
  padding-top: 34px;
  transform: translateY(-40.7px);
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
}

@media screen and (max-width: 919px) {
  .popup__title {
    transform: translateY(-30px);
    font-size: 18px;
    line-height: 22px;
    margin-left: 20px;
    padding-top: 30px;
  }
}

.popup__form {
  margin: 0 auto;
  width: 358px;
  transform: translateY(-40.7px);
  padding-top: 24.26px;
}

@media screen and (max-width: 919px) {
  .popup__form {
    width: 200px;
    padding-top: 16px;
    transform: translateY(-30px);
  }
}

.popup__form_type_delete {
  padding-top: 0;
}

.popup__input {
  padding: 29.74px 0 13.34px;
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  border: 0;
  border-bottom: 1px solid rgb(0, 0, 0, 0.2);
  box-shadow: none;
}

.popup__input:first-of-type {
  padding-bottom: 13.26px;
}

@media screen and (max-width: 919px) {
  .popup__input {
    font-size: 12px;
    line-height: 15px;
    padding: 20px 0 12px;
  }

  .popup__input:first-of-type {
    padding-bottom: 12px;
  }
}

.popup__input_type_error {
  border-bottom-color: #f00;
}

.popup__figure {
  display: flex;
  flex-direction: column;
  margin: 0;
}

.popup__image {
  max-width: 75vw;
  max-height: 75vh;
}

.popup__caption {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #fff;
  padding-top: 10px;
}

.popup__error_visible {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #f00;
}



.popup__button_disabled {
  background-color: #fff;
  color: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}

.popup__button_disabled:hover {
  opacity: 1;
}



.popup {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(0, 0, 0, 0.5);
  display: grid;
  visibility: hidden;
  place-items: center;
  opacity: 0;
  transition: visibility 0s ease 0.5s, opacity 0.5s ease;
  transition-delay: 0.5s, 0s;
}

.popup_type_image {
  background-color: rgba(0, 0, 0, 0.9);
}

.popup_opened {
  visibility: visible;
  opacity: 1;
  transition-delay: 0s;
}



